:root {
  --color-deep-blue: #1a2f4a;
  --color-blue-mid: #2d4a6a;
  --color-teal: #3a7d7c;
  --color-green: #4a9d7e;
  --color-gold: #d4af37;
  --color-organic-green: #637e6d;
  --color-gold-light: #f0d98e;
  --color-white: #fff;
  --color-off-white: #f8f9fa;
  --color-gray-light: #e0e4e8;
  --color-gray: #6c757d;
  --color-dark: #212529;
  --font-base: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  --font-size-base: 16px;
  --font-size-lg: 1.25rem;
  --font-size-xl: 1.75rem;
  --font-size-2xl: 2.5rem;
  --font-size-3xl: 3.5rem;
  --spacing-xs: .5rem;
  --spacing-sm: 1rem;
  --spacing-md: 2rem;
  --spacing-lg: 4rem;
  --spacing-xl: 6rem;
  --max-width: 1200px;
  --transition: .3s ease;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
  font-size: var(--font-size-base);
}

body {
  font-family: var(--font-base);
  color: var(--color-dark);
  background: linear-gradient(135deg, var(--color-deep-blue) 0%, var(--color-teal) 50%, var(--color-green) 100%);
  min-height: 100vh;
  line-height: 1.6;
}

.container {
  max-width: var(--max-width);
  padding: 0 var(--spacing-md);
  margin: 0 auto;
}

.nav {
  padding: var(--spacing-xs) 0;
  z-index: 100;
  background: #1a2f4ab3;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.nav-container {
  max-width: var(--max-width);
  padding: 0 var(--spacing-md);
  justify-content: space-between;
  align-items: center;
  gap: var(--spacing-md);
  margin: 0 auto;
  display: flex;
  position: relative;
}

.hamburger {
  cursor: pointer;
  z-index: 101;
  background: none;
  border: none;
  flex-direction: column;
  gap: 5px;
  padding: 8px;
  display: none;
}

.hamburger span {
  background: var(--color-white);
  width: 25px;
  height: 3px;
  transition: all .3s;
  display: block;
}

.hamburger.active span:first-child {
  transform: rotate(45deg)translate(8px, 8px);
}

.hamburger.active span:nth-child(2) {
  opacity: 0;
}

.hamburger.active span:nth-child(3) {
  transform: rotate(-45deg)translate(7px, -7px);
}

.nav-right {
  align-items: center;
  gap: var(--spacing-md);
  display: flex;
  position: relative;
}

.nav-menu {
  align-items: center;
  gap: var(--spacing-md);
  display: flex;
}

.nav-logo-link {
  background: #0000000d;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  padding: 4px;
  transition: transform .3s, background .3s;
  display: flex;
}

.nav-logo-link:hover {
  background: #00000026;
  transform: scale(1.1);
}

.nav-logo-img {
  width: auto;
  height: 50px;
}

.logo-option1 .nav-logo-img {
  filter: saturate(.6);
}

.logo-option2 .nav-logo-img {
  filter: grayscale(.3) saturate(.7);
}

.logo-option3 .nav-logo-img {
  filter: brightness(.85) contrast(.9);
}

.logo-option4 .nav-logo-img {
  filter: sepia(.15) saturate(.7) brightness(.9);
}

.logo-option5 .nav-logo-img {
  opacity: .75;
  filter: saturate(.8);
}

.logo-option6 .nav-logo-img {
  filter: saturate(.5) brightness(.9) contrast(.85);
}

.nav-link {
  color: var(--color-white);
  letter-spacing: .1em;
  transition: color var(--transition), opacity var(--transition);
  opacity: .95;
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
}

.nav-link:hover, .nav-link:focus {
  opacity: 1;
  color: var(--color-gold-light);
  outline: none;
}

.instagram-icon {
  align-items: center;
  display: flex;
}

.hero {
  text-align: center;
  min-height: 100vh;
  padding: var(--spacing-xl) var(--spacing-md);
  background: linear-gradient(135deg, #1a2f4ab3 0%, #2d4a6ab3 100%), url("hero.2c1df288.png") center / cover no-repeat;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.hero-content {
  max-width: 900px;
  animation: 1s ease-out fadeInUp;
}

.hero-brand {
  color: var(--color-organic-green);
  letter-spacing: .1em;
  text-transform: uppercase;
  margin-bottom: var(--spacing-md);
  text-shadow: 2px 2px 8px #0000004d;
  font-size: 3rem;
  font-weight: 700;
}

.brand-oswald {
  font-family: Oswald, sans-serif;
}

.hero-title {
  font-size: var(--font-size-3xl);
  color: var(--color-white);
  margin-bottom: var(--spacing-md);
  letter-spacing: -.02em;
  font-weight: 700;
  line-height: 1.2;
}

.hero-subtitle {
  font-size: var(--font-size-xl);
  color: var(--color-gold-light);
  opacity: .95;
  font-weight: 400;
}

.mission {
  background: var(--color-white);
  padding: var(--spacing-xl) var(--spacing-md);
  position: relative;
}

.mission:before {
  content: "";
  background: linear-gradient(90deg, var(--color-teal) 0%, var(--color-green) 50%, var(--color-gold) 100%);
  height: 4px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.mission-content {
  max-width: 800px;
  margin: 0 auto;
}

.mission h2 {
  font-size: var(--font-size-2xl);
  color: var(--color-deep-blue);
  margin-bottom: var(--spacing-md);
  text-align: center;
  padding-bottom: var(--spacing-sm);
  font-weight: 600;
  position: relative;
}

.mission h2:after {
  content: "";
  background: linear-gradient(90deg, var(--color-teal), var(--color-gold));
  width: 60px;
  height: 3px;
  margin: var(--spacing-sm) auto 0;
  border-radius: 2px;
  display: block;
}

.mission-text {
  font-size: var(--font-size-lg);
  color: var(--color-deep-blue);
  text-align: center;
  margin-bottom: var(--spacing-md);
  line-height: 1.8;
}

.mission-text:last-child {
  margin-bottom: 0;
}

.about {
  background: linear-gradient(135deg, var(--color-off-white) 0%, var(--color-gray-light) 100%);
  padding: var(--spacing-xl) var(--spacing-md);
  position: relative;
}

.about-content {
  max-width: 800px;
  margin: 0 auto;
}

.about h2 {
  font-size: var(--font-size-2xl);
  color: var(--color-deep-blue);
  margin-bottom: var(--spacing-md);
  text-align: center;
  padding-bottom: var(--spacing-sm);
  font-weight: 600;
  position: relative;
}

.about h2:after {
  content: "";
  background: linear-gradient(90deg, var(--color-teal), var(--color-gold));
  width: 60px;
  height: 3px;
  margin: var(--spacing-sm) auto 0;
  border-radius: 2px;
  display: block;
}

.about-text {
  font-size: var(--font-size-lg);
  color: var(--color-blue-mid);
  text-align: center;
  margin-bottom: var(--spacing-md);
  line-height: 1.8;
}

.about-text:last-child {
  margin-bottom: 0;
}

.cta {
  background: linear-gradient(135deg, var(--color-teal) 0%, var(--color-green) 100%);
  padding: var(--spacing-xl) var(--spacing-md);
  text-align: center;
}

.cta-title {
  font-size: var(--font-size-2xl);
  color: var(--color-white);
  margin-bottom: var(--spacing-md);
  font-weight: 600;
}

.cta-text {
  font-size: var(--font-size-lg);
  color: var(--color-white);
  margin-bottom: var(--spacing-lg);
  opacity: .95;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.cta-button {
  padding: var(--spacing-sm) var(--spacing-lg);
  background: var(--color-gold);
  color: var(--color-deep-blue);
  font-size: var(--font-size-lg);
  transition: all var(--transition);
  border-radius: 50px;
  font-weight: 600;
  text-decoration: none;
  display: inline-block;
  box-shadow: 0 4px 15px #0003;
}

.cta-button:hover, .cta-button:focus {
  background: var(--color-gold-light);
  outline: 2px solid var(--color-gold-light);
  outline-offset: 2px;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px #0000004d;
}

.footer {
  background: var(--color-deep-blue);
  padding: var(--spacing-lg) var(--spacing-md);
  text-align: center;
}

.footer-content {
  justify-content: center;
  align-items: center;
  gap: var(--spacing-sm);
  margin-bottom: var(--spacing-sm);
  flex-wrap: wrap;
  display: flex;
}

.footer-link {
  color: var(--color-gold-light);
  transition: color var(--transition);
  text-decoration: none;
}

.footer-link:hover, .footer-link:focus {
  color: var(--color-gold);
  outline: none;
}

.footer-divider {
  color: var(--color-gray);
}

.footer-text {
  color: var(--color-white);
  opacity: .8;
}

.footer-tagline {
  color: var(--color-white);
  opacity: .6;
  font-size: .9rem;
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in {
  opacity: 0;
  animation: .8s ease-out forwards fadeInUp;
}

@media (width <= 768px) {
  :root {
    --font-size-3xl: 2rem;
    --font-size-2xl: 1.75rem;
    --font-size-xl: 1.25rem;
    --font-size-lg: 1.1rem;
    --spacing-xl: 3rem;
    --spacing-lg: 2rem;
  }

  .hero {
    padding: var(--spacing-lg) var(--spacing-md);
  }

  .hero-title {
    font-size: var(--font-size-2xl);
  }

  .hero-subtitle {
    font-size: var(--font-size-lg);
  }

  .mission {
    padding: var(--spacing-lg) var(--spacing-md);
  }

  .mission h2, .about h2 {
    font-size: var(--font-size-xl);
  }

  .mission-text, .about-text {
    font-size: var(--font-size-base);
    line-height: 1.7;
  }

  .about, .cta {
    padding: var(--spacing-lg) var(--spacing-md);
  }

  .cta-title {
    font-size: var(--font-size-xl);
  }

  .cta-text {
    font-size: var(--font-size-base);
  }

  .cta-button {
    padding: var(--spacing-sm) var(--spacing-md);
    font-size: var(--font-size-base);
  }

  .hamburger {
    display: flex;
  }

  .nav-menu {
    padding: var(--spacing-md);
    gap: var(--spacing-sm);
    opacity: 0;
    visibility: hidden;
    background: #1a2f4af2;
    border-radius: 0 0 0 8px;
    flex-direction: column;
    align-items: flex-start;
    min-width: 200px;
    transition: transform .3s, opacity .3s, visibility .3s;
    position: absolute;
    top: 100%;
    right: 0;
    transform: translateX(100%);
    box-shadow: -4px 4px 12px #0000004d;
  }

  .nav-menu.active {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
  }

  .hero-brand {
    letter-spacing: .08em;
    margin-bottom: var(--spacing-md);
    font-size: 2rem;
  }
}

@media (width <= 480px) {
  .container {
    padding: 0 var(--spacing-sm);
  }

  .nav-container {
    padding: 0 var(--spacing-sm);
    gap: var(--spacing-sm);
  }
}
